	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>Message 1592 : Sun Aug 23 (Azriya)     :: War in the East</p>\n</blockquote>\n<p>The forests south of the Mountains of Mirkwood have long been contested\nground, and now is no different.  The trade route through Mirkwood is\nprofitable for those travelling it and also those raiding the travellers.  The\norcs have allowed their Uruk-hai allies to shelter within their caves, if\nsomewhat unwillingly.  As usual the arrangement between these tribes is\nstrained at best. In related news, the Lords of Gondor have made an agreement\nwith Troy Volrath to allow the Free Peoples some respite when needed.</p>\n<p>Credit:  Turamarth and Orome</p>","frontmatter":{"title":"News 1592: War in the East","date":"October 09, 2017","author":null,"desc":null}}},"pathContext":{"slug":"/news/1592"}}

